@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  background: #f0f0f0;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #080808;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
}

main {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.nav-links li {
  margin: 0.5rem 1rem;
  text-transform: uppercase;
}

.nav-links li a {
  color: #ccc;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  color: orangered;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (max-width: 767px) {
  .nav-links li {
    padding: 0 1rem 0.5rem;
    margin: 0.5rem 1rem;
    border-bottom: 1px solid #999;
    width: 100%;
  }
  .nav-links li a {
    color: #ccc;
    text-decoration: none;
    padding: 0.5rem;
    width: 100%;
    display: block;
  }
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  /* .nav-links li {
    margin: 0.5rem;
  } */

  .nav-links a {
    color: white;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: #f8df00;
    color: #292929;
  }
}

.side-drawer {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: #282828;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.slide-in-left-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.slide-in-left-enter-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

/**/
.slide-in-right-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.slide-in-right-enter-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-right-exit {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  opacity: 1;
}

.slide-in-right-exit-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 0;
  transition: all 200ms;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.main-navigation__menu-btn {
  width: 2.5rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-right: 2rem;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 0.8rem;
}

.main-navigation__menu-btn span {
  display: block;
  width: 2.5rem;
  height: 4px;
  background: #ddd;
  margin: 0 5px 6px;
  padding: 0;
}

.main-navigation__title {
  color: white;
}

.main-navigation__title a {
  text-decoration: none;
  color: white;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}
.main-navigation__title a.logo {
  position: relative;
  top: 0.5rem;
  left: 1rem;
}
.main-navigation__title a.logo > img {
  height: 70px;
  width: auto;
  display: block;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.card {
  position: relative;
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
  background: white;
}

.button {
  font: inherit;
  padding: 0.5rem 1rem;
  border: 1px solid #ff0055;
  border-radius: 2rem;
  background: #ff0055;
  color: white;
  cursor: pointer;
  margin-right: 0.5rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ff4382;
  border-color: #ff4382;
}

.button--inverse {
  background: transparent;
  color: #000;
  border-color: #999;
}
.button.plain {
  background: transparent;
  color: #000;
  border: none;
}
.button.plain:hover,
.button.plain:active {
  background: transparent;
  color: #000;
  text-decoration: underline;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #e52626;
}

.button--danger {
  background: #282828;
  border-color: #282828;
}

.button--danger:hover,
.button--danger:active {
  background: #e52626;
  border-color: #e52626;
}

button.transparent {
  background: transparent;
  padding: 0;
  margin: 0;
  border: none;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
.place-item__actions button,
.place-item__actions a {
  margin: 0.5rem;
}
@media (max-width: 767px) {
  .place-item__actions button,
  .place-item__actions a {
    margin: 0 0.5rem 0.5rem 0;
  }
  .button {
    letter-spacing: -1px;
    border-radius: 0.8rem;
    padding: 0.4rem 1rem;
    margin: 0 0.5rem 0 0;
  }
}

.modal {
  z-index: 100;
  position: fixed;
  top: 10vh;
  left: 5%;
  width: 90%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 1rem;
}

.modal__header {
  width: 100%;
  padding: 0.5rem 1rem;
  background: #e52626;
  color: white;
  border-radius: 1rem 1rem 0 0;
}

.modal__header h2 {
  margin: 0.2rem 0.5rem;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center;
  line-height: 1;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.modal-enter {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.map {
  width: 100%;
  height: 100%;
}

.place-item {
  margin: 1rem auto;
  width: 30%;
}
.place-item__content {
  padding: 0;
}

.place-item__info {
  padding: 1rem;
  text-align: center;
}
.card.card_place {
  border-radius: 1rem 1rem 4px 4px;
}
.place-item__image,
.place-item__image > button.transparent {
  width: 100%;
  height: 12.5rem;
  margin-right: 1.5rem;
}

.place-item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card.place-item__content.card_place {
  box-shadow: none;
  border: 1px solid #ddd;
}

.place-detail__wrap {
  padding: 1rem 1rem 0;
}
.pointer {
  cursor: pointer;
}
.place-detail__image_wrap {
  overflow-y: auto;
  height: 250px;
  text-align: center;
  padding-top: 0;
}

.place-detail__image {
  text-align: center;
  max-width: 100%;
  object-fit: cover;
}
.detail__desc {
  overflow-y: auto;
  max-height: 130px;
}
.place-item__info h2,
.place-item__info h3,
.place-detail__wrap h3 {
  font-weight: 300;
  letter-spacing: -1px;
  margin-top: 0;
}
.place-detail__wrap h3 {
  font-size: 1.5rem;
  margin: 0 0 1rem;
}
.place-detail__creator {
  text-align: right;
  font-style: italic;
  font-size: 0.8rem;
  margin: 0;
}
.place-item__info h2,
.place-item__info h3,
.place-item__info p {
  margin: 0 0 0.1rem 0;
}
.place-item__address {
  height: 1.6rem;
  line-height: 1.6rem;
  overflow: hidden;
}
.place-item__desc {
  height: 3rem;
  overflow: hidden;
}

.place-item__actions {
  padding: 1rem;
  text-align: center;
  border-top: 1px solid #ccc;
}

.place-item__modal-content {
  padding: 0;
}

.place-item__modal-actions {
  text-align: right;
}

.map-container {
  height: 15rem;
  width: 100%;
}
@media (max-width: 767px) {
  .place-item {
    margin: 1rem auto;
    width: 90%;
  }
}

@media (min-width: 768px) {
  .place-item__image,
  .place-item__image > button.transparent {
    height: 20rem;
  }
}

.place-list {
  list-style: none;
  margin: 1rem auto;
  padding: 0;
  width: 90%;
  max-width: 100rem;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .place-list {
    width: 98%;
  }
}

