.place-item {
  margin: 1rem auto;
  width: 30%;
}
.place-item__content {
  padding: 0;
}

.place-item__info {
  padding: 1rem;
  text-align: center;
}
.card.card_place {
  border-radius: 1rem 1rem 4px 4px;
}
.place-item__image,
.place-item__image > button.transparent {
  width: 100%;
  height: 12.5rem;
  margin-right: 1.5rem;
}

.place-item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card.place-item__content.card_place {
  box-shadow: none;
  border: 1px solid #ddd;
}

.place-detail__wrap {
  padding: 1rem 1rem 0;
}
.pointer {
  cursor: pointer;
}
.place-detail__image_wrap {
  overflow-y: auto;
  height: 250px;
  text-align: center;
  padding-top: 0;
}

.place-detail__image {
  text-align: center;
  max-width: 100%;
  object-fit: cover;
}
.detail__desc {
  overflow-y: auto;
  max-height: 130px;
}
.place-item__info h2,
.place-item__info h3,
.place-detail__wrap h3 {
  font-weight: 300;
  letter-spacing: -1px;
  margin-top: 0;
}
.place-detail__wrap h3 {
  font-size: 1.5rem;
  margin: 0 0 1rem;
}
.place-detail__creator {
  text-align: right;
  font-style: italic;
  font-size: 0.8rem;
  margin: 0;
}
.place-item__info h2,
.place-item__info h3,
.place-item__info p {
  margin: 0 0 0.1rem 0;
}
.place-item__address {
  height: 1.6rem;
  line-height: 1.6rem;
  overflow: hidden;
}
.place-item__desc {
  height: 3rem;
  overflow: hidden;
}

.place-item__actions {
  padding: 1rem;
  text-align: center;
  border-top: 1px solid #ccc;
}

.place-item__modal-content {
  padding: 0;
}

.place-item__modal-actions {
  text-align: right;
}

.map-container {
  height: 15rem;
  width: 100%;
}
@media (max-width: 767px) {
  .place-item {
    margin: 1rem auto;
    width: 90%;
  }
}

@media (min-width: 768px) {
  .place-item__image,
  .place-item__image > button.transparent {
    height: 20rem;
  }
}
