.button {
  font: inherit;
  padding: 0.5rem 1rem;
  border: 1px solid #ff0055;
  border-radius: 2rem;
  background: #ff0055;
  color: white;
  cursor: pointer;
  margin-right: 0.5rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ff4382;
  border-color: #ff4382;
}

.button--inverse {
  background: transparent;
  color: #000;
  border-color: #999;
}
.button.plain {
  background: transparent;
  color: #000;
  border: none;
}
.button.plain:hover,
.button.plain:active {
  background: transparent;
  color: #000;
  text-decoration: underline;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #e52626;
}

.button--danger {
  background: #282828;
  border-color: #282828;
}

.button--danger:hover,
.button--danger:active {
  background: #e52626;
  border-color: #e52626;
}

button.transparent {
  background: transparent;
  padding: 0;
  margin: 0;
  border: none;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
.place-item__actions button,
.place-item__actions a {
  margin: 0.5rem;
}
@media (max-width: 767px) {
  .place-item__actions button,
  .place-item__actions a {
    margin: 0 0.5rem 0.5rem 0;
  }
  .button {
    letter-spacing: -1px;
    border-radius: 0.8rem;
    padding: 0.4rem 1rem;
    margin: 0 0.5rem 0 0;
  }
}
