.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.nav-links li {
  margin: 0.5rem 1rem;
  text-transform: uppercase;
}

.nav-links li a {
  color: #ccc;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  color: orangered;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (max-width: 767px) {
  .nav-links li {
    padding: 0 1rem 0.5rem;
    margin: 0.5rem 1rem;
    border-bottom: 1px solid #999;
    width: 100%;
  }
  .nav-links li a {
    color: #ccc;
    text-decoration: none;
    padding: 0.5rem;
    width: 100%;
    display: block;
  }
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  /* .nav-links li {
    margin: 0.5rem;
  } */

  .nav-links a {
    color: white;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: #f8df00;
    color: #292929;
  }
}
