.modal {
  z-index: 100;
  position: fixed;
  top: 10vh;
  left: 5%;
  width: 90%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 1rem;
}

.modal__header {
  width: 100%;
  padding: 0.5rem 1rem;
  background: #e52626;
  color: white;
  border-radius: 1rem 1rem 0 0;
}

.modal__header h2 {
  margin: 0.2rem 0.5rem;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center;
  line-height: 1;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}
