.main-navigation__menu-btn {
  width: 2.5rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-right: 2rem;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 0.8rem;
}

.main-navigation__menu-btn span {
  display: block;
  width: 2.5rem;
  height: 4px;
  background: #ddd;
  margin: 0 5px 6px;
  padding: 0;
}

.main-navigation__title {
  color: white;
}

.main-navigation__title a {
  text-decoration: none;
  color: white;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}
.main-navigation__title a.logo {
  position: relative;
  top: 0.5rem;
  left: 1rem;
}
.main-navigation__title a.logo > img {
  height: 70px;
  width: auto;
  display: block;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}
